<template>
    <router-view></router-view>
</template>

<script>
  import axios from "../../axios";

  export default {
    mounted() {
      if(this.$router.currentRoute.name != 'bacnet-networks'){
        this.getNetworks();
      }
    },
    methods: {
      getNetworks() {
        axios.get( 'bacnet_master/networks' )
          .then((data) => {
            this.$store.commit('changeBmNetworksData', data.networks);
          });
      },
    },
  };
</script>


